* {
  padding: 0;
  margin: 0;
  color: #000;
}
canvas {
  display: block;
  background: #000
    url(http://www.script-tutorials.com/demos/360/images/stars.png) repeat top
    center;
  z-index: 0;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.current-score,
.best-score {
  left: 10%;
  color: #fff;
  font-size: 2rem;
  z-index: 100000;
}
.best-score {
  left: 50%;
}
.score {
  display: block;
  position: absolute;
  top: 15px;
  z-index: 1;
  font-size: 2rem;
}
.controls {
  display: block;
  color: red;
  position: absolute;
  top: 15px;
  left: 95%;
  -webkit-transform: translate(-50%, 0);
      -ms-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  z-index: 100000;
  font-size: 2rem;
  text-align: center;
  line-height: 1.6;
}
.controls i {
  font-size: 2rem;
  color: red;
}
.endgame {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 16px;
  z-index: 1;
  text-align: center;
}
.endgame p {
  color: #fff;
  font-size: 2rem;
}
button {
  border: 4px solid #ffffff;
  background-color: transparent;
  color: #ffffff;
  font-size: 20px;
  padding: 10px 20px;
  margin: 10px;
  font-family: "PT Mono", serif;
  cursor: pointer;
}
button:hover {
  border: 5px solid #fff;
}



